
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: serif;
}
body{
overflow-x: hidden ;
}
section{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;

}
section h2{
    font-size: 6em;
    color: #fff;
    font-weight: 500;
}
.box{
     position: relative;
    width: 100%;
    min-height: 100vh;
    padding: 100px;
}
.box h2{
font-size: 3rem;
}
.box .logo{
width: 300px;

}
.box2{
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding: 100px;
    background: #000;
}
.box2 h2{
    font-size: 3rem;
    color: #fff;
}